.footer {
	background-color: $sub-color;
	padding-top: 55px;
	&__container {
	}

	&__inner {
		display: flex;
		justify-content: space-between;

		margin-bottom: 50px;
		@media (max-width: $md2+px) {
			flex-direction: column;
		}
	}

	&__logo {
		flex: 0 0 auto;
		display: inline-block;
		img {
			height: 65px;
			width: 180px;
		}
		@media (max-width: $md2+px) {
		}
	}

	&__list-social {
		flex: 0 0 10%;
		display: flex;
		justify-content: flex-end;
		padding-top: 25px;
		li:not(:last-child) {
			margin-right: 15px;
		}
		@media (max-width: $md2+px) {
			justify-content: center;
			flex: 0 0 100%;
		}
	}

	&__link--inst,
	&__link--fb {
		display: inline-block;
		padding-bottom: 5px;
		border-bottom: 1px solid transparent;

		transition: border-bottom 0.2s ease-out;
		svg {
			fill: #fff;
			width: 22px;
			height: 23px;
		}
		@media (min-width: $md3+px) {
			&:hover {
				border-bottom: 1px solid #fff;
			}
		}
	}

	&__outer {
		margin-bottom: 50px;
	}
}

.acordion {
	&__wrapper {
		flex: 0 0 23.3333%;
		padding: 0 20px;
		padding-top: 25px;
		@media (max-width: $md2+px) {
			padding: 0 0px;
			padding-top: 25px;
		}
	}

	&__title {
		margin-bottom: 16px;
		color: #fff;

		font-weight: bold;
		font-size: 18px;
	}

	&__body {
		font-size: 18px;
		font-weight: 400;
		line-height: 23px;
	}
	&__body--uppercase {
		text-transform: uppercase;
	}
	&__item {
		&:not(:last-child) {
			margin-bottom: 10px;
		}
		@media (max-width: $md2+px) {
			&:first-child {
				margin-top: 25px;
			}
		}
	}

	&__link {
		display: inline-block;
		color: #fff;
		border-bottom: 1px solid transparent;

		transition: border-bottom 0.2s ease-out;
		@media (min-width: $md3+px) {
			&:hover {
				border-bottom: 1px solid #fff;
			}
		}
	}
}

@media (max-width: $md2+px) {
	.acordion {
		&__title {
			cursor: pointer;
		}
		&__body {
			display: block;
			overflow: hidden;
			height: auto;
			max-height: 0px;
			opacity: 0;
			transition: all 0.5s ease;
		}

		.acordion__wrapper.acordion--active &__body {
			max-height: 1400px;
			opacity: 1;
		}
	}

	.acordion-triger {
		position: relative;
		padding-right: 60px;
		margin-bottom: 0;
		transition: all 0.5s ease;
		&::after {
			content: "";
			position: absolute;
			top: 0;
			right: 0;

			width: 16px;
			height: 16px;
			background: url(../img/sort-down.svg) center / cover no-repeat;
			transform: translateY(25%);
			transition: all 0.3s ease;
		}
	}

	.acordion--active .acordion-triger {
		&::after {
			transform: rotate(180deg);
		}
	}
}

/* Снэк-бар - расположите ее внизу и в середине экрана */
#snackbar-cart {
	visibility: hidden; /* Скрыто по умолчанию. Видимый по щелчку мыши */
	min-width: 250px; /* Установите минимальную ширину по умолчанию */
	width: 90%;
	margin-left: -125px; /* Разделите значение min-width на 2 */
	background-color: #333; /* Черный цвет фона */
	color: #fff; /* Белый цвет текста */
	text-align: center; /* Центрированный текст */
	border-radius: 2px; /* Округленные границы */
	padding: 16px; /* Отступ */
	position: fixed; /* Сядьте на верхнюю часть экрана */
	z-index: 1; /* Добавить z-индекс при необходимости */
	right: 5%; /* Центр, снэк-бар */
	bottom: 30px; /* 30 пикселей снизу */
}

/* Показывать снэк-бар при нажатии на кнопку (Класс добавлен с помощью JavaScript) */
#snackbar-cart.show {
	visibility: visible; /* Покажите снэк-бар */
	/* Добавить анимацию: Возьмите 0,5 секунды, чтобы исчезать и выходить из закусочной.
	Однако задержите процесс затухания на 2,5 секунды */
	-webkit-animation: fadein 0.5s, fadeout 0.5s 2.8s;
	animation: fadein 0.5s, fadeout 0.5s 2.8s;
}

#snackbar-rating {
	visibility: hidden; /* Скрыто по умолчанию. Видимый по щелчку мыши */
	min-width: 250px; /* Установите минимальную ширину по умолчанию */
	width: 90%;
	margin-left: -125px; /* Разделите значение min-width на 2 */
	background-color: #333; /* Черный цвет фона */
	color: #fff; /* Белый цвет текста */
	text-align: center; /* Центрированный текст */
	border-radius: 2px; /* Округленные границы */
	padding: 16px; /* Отступ */
	position: fixed; /* Сядьте на верхнюю часть экрана */
	z-index: 1; /* Добавить z-индекс при необходимости */
	right: 5%; /* Центр, снэк-бар */
	bottom: 30px; /* 30 пикселей снизу */
}

/* Показывать снэк-бар при нажатии на кнопку (Класс добавлен с помощью JavaScript) */
#snackbar-rating.show {
	visibility: visible; /* Покажите снэк-бар */
	/* Добавить анимацию: Возьмите 0,5 секунды, чтобы исчезать и выходить из закусочной.
	Однако задержите процесс затухания на 2,5 секунды */
	-webkit-animation: fadein 0.5s, fadeout 0.5s 2.8s;
	animation: fadein 0.5s, fadeout 0.5s 2.8s;
}
/* Анимация, чтобы исчезать снэк-бар в и из */
@-webkit-keyframes fadein {
	from {
		bottom: 0;
		opacity: 0;
	}
	to {
		bottom: 30px;
		opacity: 1;
	}
}

@keyframes fadein {
	from {
		bottom: 0;
		opacity: 0;
	}
	to {
		bottom: 30px;
		opacity: 1;
	}
}

@-webkit-keyframes fadeout {
	from {
		bottom: 30px;
		opacity: 1;
	}
	to {
		bottom: 0;
		opacity: 0;
	}
}

@keyframes fadeout {
	from {
		bottom: 30px;
		opacity: 1;
	}
	to {
		bottom: 0;
		opacity: 0;
	}
}
