.promo {
	height: 100%;
	background: url(../img/main-page-bg-promo.jpg) center/cover no-repeat;
	min-height: 100vh;
	display: flex;
	align-items: flex-end;
	justify-content: flex-start;
	&__container {
		padding-top: 150px;
		height: 100%;
		width: 100%;

		display: flex;
		flex-direction: column;
		justify-content: flex-end;

		padding-bottom: 230px;
	}

	&__title {
		margin-bottom: 20px;
		color: #fff;
	}

	&__title--small {
		display: block;

		font-size: 24px;
		font-weight: 400;
		text-transform: uppercase;
	}

	&__btn {
		align-self: flex-start;
		padding: 6px 26px;

		text-transform: uppercase;
	}
	@media (max-width: $md2+px) {
		&__container {
			padding-bottom: 150px;
		}
	}
	@media (max-width: $md3+px) {
		&__container {
			justify-content: center;
			align-items: center;
			text-align: center;
			padding-bottom: 100px;
		}
		&__title--small {
			font-size: 20px;
			margin-bottom: 5px;
		}
		&__btn {
			align-self: center;
		}
	}
	@media (max-width: $md4+px) {
		&__container {
			padding-bottom: 100px;
		}
	}
	@media (max-height: 720px) {
		&__container {
			padding-bottom: 100px;
		}
	}
	@media (max-height: $md4+px) and (orientation: landscape) {
		&__container {
			justify-content: flex-start;
		}
	}
}
