@charset "UTF-8";
*,
html {
  margin: 0;
  padding: 0;
  border: 0;
}

*,
*:before,
*:after {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

html,
body {
  height: 100%;
  width: 100%;
  font-size: 100%;
  line-height: 1;
  font-size: 14px;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

input,
button,
textarea {
  font-family: inherit;
}

input::-ms-clear {
  display: none;
}

button {
  cursor: pointer;
}

button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

a,
a:visited {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

ul li {
  list-style: none;
}

ol {
  list-style-position: inside;
}

img {
  vertical-align: top;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: inherit;
  font-weight: 400;
  margin: 0;
}

[type="search"]::-webkit-search-cancel-button,
[type="search"]::-webkit-search-decoration {
  -webkit-appearance: none;
  appearance: none;
}

@font-face {
  font-family: "FuturaPT";
  src: url("../fonts/FuturaPT-Bold.woff2") format("woff2"), url("../FuturaPT-Bold.woff") format("woff");
  font-weight: 700;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "FuturaPT";
  src: url("../fonts/FuturaPT-Book.woff2") format("woff2"), url("../FuturaPT-Book.woff") format("woff");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "FuturaPT";
  src: url("../fonts/FuturaPT-Medium.woff2") format("woff2"), url("../FuturaPT-Medium.woff") format("woff");
  font-weight: 500;
  font-display: swap;
  font-style: normal;
}

/* Шрифты
@include font-face("FuturaPT", "FuturaPT-Bold", 700);
@include font-face("FuturaPT", "FuturaPT-Book", 400);
@include font-face("FuturaPT", "FuturaPT-Medium", 500);
*/
.menu__wrapper {
  -ms-overflow-style: none;
}

.menu__wrapper::-webkit-scrollbar {
  width: 0px;
  background: transparent;
}

html {
  min-height: 100%;
  height: 100%;
}

body {
  position: relative;
  height: 100%;
  min-height: 100%;
  font-family: "FuturaPT", sans-serif;
  font-weight: 400;
}

.wrapper {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

.lock {
  overflow: hidden;
}

.container {
  max-width: 1330px;
  padding-left: 15px;
  padding-right: 15px;
  margin: 0 auto;
}

@media (max-width: 991.98px) and (orientation: portrait) {
  .container {
    padding-left: 10px;
    padding-right: 10px;
  }
}

@media (max-width: 767.98px) and (orientation: portrait) {
  .container {
    padding-left: 10px;
    padding-right: 10px;
  }
}

@media (max-height: 479.98px) and (orientation: landscape) {
  .container {
    padding-left: 50px;
    padding-right: 50px;
  }
}

.visually-hidden {
  position: absolute !important;
  clip: rect(1px 1px 1px 1px);
  /* IE6, IE7 */
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0 !important;
  border: 0 !important;
  height: 1px !important;
  width: 1px !important;
  overflow: hidden;
}

.logo {
  color: #fff;
  font-weight: 700;
  transition: transform 0.3s ease;
}

.logo img {
  height: 65px;
  width: 160px;
}

@media (min-width: 767.98px) {
  .logo:hover {
    transform: scale(1.05);
  }
  .logo:focus {
    transform: scale(1.05);
  }
}

.main-title {
  font-family: "FuturaPT", sans-serif;
  font-weight: 700;
  font-size: 64px;
  color: #000;
}

@media (max-width: 1312px) {
  .main-title {
    font-size: 50px;
  }
}

@media (max-width: 991.98px) {
  .main-title {
    font-size: 36px;
  }
}

@media (max-width: 479.98px) {
  .main-title {
    font-size: 26px;
  }
}

.sub-title {
  color: #000;
  font-family: "FuturaPT", sans-serif;
  font-weight: 700;
  font-size: 36px;
}

@media (max-width: 991.98px) {
  .sub-title {
    font-size: 26px;
  }
}

@media (max-width: 767.98px) {
  .sub-title {
    font-size: 26px;
    font-size: 20px;
  }
}

.text {
  font-family: "FuturaPT", sans-serif;
  font-size: 18px;
  line-height: 23px;
}

.btn-reset {
  border: none;
  margin: 0;
  padding: 0;
  width: auto;
  overflow: visible;
  background: transparent;
  color: inherit;
  font: inherit;
  line-height: normal;
  -webkit-font-smoothing: inherit;
  -moz-osx-font-smoothing: inherit;
  -webkit-appearance: none;
}

.btn-reset::-moz-focus-inner {
  border: 0;
  padding: 0;
}

.btn-link-black {
  display: inline-block;
  padding-top: 11px;
  padding-bottom: 11px;
  color: #fff;
  background-color: #000;
  border: 3px solid #000000;
  text-align: center;
  text-transform: uppercase;
  font-weight: 700;
  font-size: 18px;
  line-height: 1;
  transition: all 0.3s ease;
}

.btn-link-black:hover {
  color: #000;
  background-color: white;
}

.btn-link-black:focus {
  color: #000;
  background-color: white;
}

.btn-link-black:disabled {
  color: #fff;
  background-color: #000;
  border: 3px solid #000000;
  opacity: 0.5;
  cursor: default;
}

.btn-link-transparent {
  display: inline-block;
  width: 100%;
  padding-top: 11px;
  padding-bottom: 11px;
  color: #000;
  background-color: transparent;
  border: 3px solid #000000;
  text-align: center;
  text-transform: uppercase;
  font-weight: 700;
  font-size: 18px;
  line-height: 1;
  transition: all 0.3s ease;
}

.btn-link-transparent:hover {
  color: #fff;
  background-color: #000000;
}

.btn-link-transparent:focus {
  color: #fff;
  background-color: #000000;
}

.btn-link-transparent:disabled {
  color: #fff;
  background-color: #000;
  border: 3px solid #000000;
  opacity: 0.5;
  cursor: default;
}

.input {
  display: inline-block;
  height: 40px;
  padding-left: 20px;
  color: #000;
  background-color: transparent;
  border: 3px solid #000000;
  outline: none;
  font-size: 18px;
  font-weight: 400;
  text-transform: none;
  transition: all 0.3s ease;
}

.input::placeholder {
  font-family: "FuturaPT", sans-serif;
}

.input:hover {
  color: #fff;
  background-color: #000000;
}

.input:focus {
  color: #fff;
  background-color: #000000;
}

.copy {
  color: #484848;
  font-weight: 500;
  font-size: 18px;
  text-align: center;
}

.header {
  position: relative;
  width: 100%;
}

.nav {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 50;
  width: 100%;
  padding-top: 48px;
  transition: all 0.2s ease;
}

.nav::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  z-index: 2;
  width: 100%;
  height: 100%;
  background-color: transparent;
  transition: all 0.2s ease;
}

.nav.nav--active {
  padding-top: 0px;
}

.nav.nav--active::before {
  background: #151515;
}

.nav__body {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 100%;
  transition: all 0.2s ease;
}

.nav__logo {
  position: relative;
  z-index: 2;
}

.nav__form-wrapper {
  position: relative;
}

.nav__form {
  position: relative;
  z-index: 4;
  padding-right: 75px;
}

.nav__submit {
  position: absolute;
  top: 50%;
  left: 17px;
  width: 14px;
  height: 14px;
  transform: translateY(-50%);
}

.nav__submit svg {
  width: 14px;
  height: 14px;
  transition: transform 0.3s ease;
}

@media (min-width: 767.98px) {
  .nav__submit svg:hover {
    transform: scale(1.2);
  }
}

.nav__submit:hover .nav__search {
  border: 1px solid #f2f2f2;
}

.nav__search {
  width: 400px;
  height: 30px;
  padding-left: 42px;
  padding-right: 10px;
  outline: none;
  background: #ffffff;
  border-radius: 15.5px;
  border: 1px solid transparent;
  font-size: 18px;
  font-weight: 500;
  transition: border 0.2s ease;
}

@media (min-width: 767.98px) {
  .nav__search:hover {
    border: 1px solid #000;
  }
  .nav__search:focus {
    border: 1px solid #000;
  }
}

.nav__search.active {
  border-bottom-left-radius: 0;
  border-bottom-right-radius: 0;
}

.nav__icon-btns {
  display: flex;
  align-items: center;
}

.nav__cart-btn {
  position: relative;
  z-index: 7;
  display: flex;
  align-self: center;
  margin-right: 35px;
  outline: none;
  background: transparent;
  transition: all 0.3s ease;
}

@media (min-width: 767.98px) {
  .nav__cart-btn:focus svg {
    transform: scale(1.3);
  }
}

.nav__cart-btn svg {
  width: 25px;
  height: 25px;
  fill: white;
  transition: all 0.3s ease;
}

@media (min-width: 767.98px) {
  .nav__cart-btn svg:hover {
    transform: scale(1.1);
  }
}

.nav__cart-btn.active {
  background-color: #fff;
  border-radius: 100%;
}

.nav__cart-btn.active svg {
  fill: #000;
}

@media (max-width: 991.98px) {
  .nav__form {
    padding-right: 0;
  }
}

@media (max-width: 767.98px) {
  .nav {
    padding-top: 5px;
  }
  .nav.nav--active {
    padding-top: 5px;
  }
  .nav__form-wrapper {
    width: 100%;
  }
  .nav__body {
    flex-wrap: wrap;
    padding-bottom: 5px;
  }
  .nav__icon-btns {
    flex: 0 0 50%;
    display: flex;
    justify-content: flex-end;
  }
  .nav__logo {
    flex: 0 0 50%;
  }
  .nav__logo img {
    height: 45px;
    width: 120px;
  }
  .nav__form {
    width: 100%;
  }
  .nav__search {
    width: 100%;
  }
}

.cart {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 6;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  visibility: hidden;
  height: 100vh;
  width: 100%;
  transform: translateX(100%);
  transition: transform 0.3s ease, visibility 0.3s ease;
}

.cart.active-flex {
  display: flex;
}

.cart.active {
  visibility: visible;
  transform: translateX(0px);
}

.cart.active .cart__close-field {
  opacity: 1;
}

.cart__close-field {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 2;
  height: 100%;
  width: 100%;
  opacity: 0;
  background-color: rgba(0, 0, 0, 0.7);
  cursor: pointer;
  transition: opacity 0.3s ease 0.2s;
}

.cart__wrapper {
  position: relative;
  z-index: 6;
  overflow-y: auto;
  width: 25%;
  height: 100%;
  padding-top: 70px;
  background-color: #fff;
  transition: padding 0.2s ease;
}

.cart__wrapper.cart__wrapper--onscroll {
  padding-top: 20px;
}

.cart__quantity {
  padding: 0 30px;
  padding-bottom: 5px;
  font-weight: 700;
  font-size: 18px;
}

.cart__item {
  padding: 10px 0;
  padding-right: 24px;
  padding-left: 32px;
}

.cart__item:not(:last-child) {
  border-bottom: 1px solid black;
}

.cart__sum {
  padding: 0 27px;
  padding-top: 20px;
  padding-bottom: 17px;
  font-weight: 700;
  font-size: 18px;
  text-align: right;
}

.cart__btn-order {
  width: 100%;
  padding-top: 17px;
  padding-bottom: 16px;
}

@media (max-width: 4400px) and (min-width: 2000px) {
  .cart__btn-order {
    padding-left: 32px;
    text-align: left;
  }
  .cart__wrapper {
    width: 45%;
  }
  .cart__sum {
    text-align: left;
  }
}

@media (max-width: 3400px) and (min-width: 2000px) {
  .cart__btn-order {
    padding-left: 32px;
    text-align: left;
  }
  .cart__wrapper {
    width: 40%;
  }
  .cart__sum {
    text-align: left;
  }
}

@media (max-width: 2000px) {
  .cart__wrapper {
    width: 35%;
  }
}

@media (max-width: 991.98px) {
  .cart__wrapper {
    width: 100%;
  }
}

@media (max-width: 767.98px) {
  .cart__wrapper {
    padding-top: 20px;
  }
  .cart__wrapper.cart__wrapper--onscroll {
    padding-top: 20px;
  }
}

@media (max-width: 479.98px) {
  .cart__quantity {
    padding: 0 10px;
  }
  .cart__item {
    padding-right: 10px;
    padding-left: 10px;
  }
}

@media (max-height: 479.98px) and (orientation: landscape) {
  .cart__quantity {
    padding: 0 50px;
  }
  .cart__item {
    padding-right: 50px;
    padding-left: 50px;
  }
}

.sum {
  font-weight: 400;
  font-size: 18px;
}

.c-item {
  position: relative;
  display: flex;
}

.c-item__image {
  margin-right: 27px;
  width: 63px;
  height: 46px;
  object-fit: contain;
}

.c-item__link {
  color: inherit;
}

.c-item__title {
  padding: 5px 0;
  font-weight: 500;
  font-size: 18px;
}

.c-item__price {
  display: block;
  font-size: 18px;
}

.c-item__delete {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 20px;
  height: 20px;
  background: #ec8e8e;
  border-radius: 100%;
  font-size: 26px;
  color: #fff;
}

.search-field {
  display: none;
  position: absolute;
  top: 15px;
  left: 0;
  z-index: 3;
  overflow: hidden;
  min-height: 130px;
  width: 400px;
  padding-top: 15px;
  border-bottom-right-radius: 15.5px;
  border-bottom-left-radius: 15.5px;
  background-color: #f2f2f2;
}

.search-field__item {
  padding-right: 17px;
  padding-left: 17px;
  padding-top: 8px;
  padding-bottom: 7px;
}

.search-field__item:not(:last-child) {
  border-bottom: 1px solid #000;
}

@media (max-width: 767.98px) {
  .search-field {
    width: 100%;
  }
}

.sf-item {
  display: flex;
  align-items: center;
  width: 100%;
  transition: background-color 0.3s ease;
}

@media (min-width: 767.98px) {
  .sf-item:hover {
    background-color: #d5d5d5;
  }
}

.sf-item__image {
  margin-right: 10px;
}

.sf-item__link {
  display: inline-block;
  color: #000;
}

.sf-item__name {
  display: block;
  max-width: 260px;
  white-space: nowrap;
  /* Запрещаем перенос строк */
  overflow: hidden;
  /* Обрезаем все, что не помещается в область */
  text-overflow: ellipsis;
  font-weight: 500;
  font-size: 18px;
  text-transform: uppercase;
}

.sf-item__subinfo {
  display: block;
  max-width: 260px;
  white-space: nowrap;
  /* Запрещаем перенос строк */
  overflow: hidden;
  /* Обрезаем все, что не помещается в область */
  text-overflow: ellipsis;
  font-size: 18px;
}

@media (max-width: 479.98px) {
  .sf-item__name {
    max-width: 180px;
  }
  .sf-item__subinfo {
    max-width: 180px;
  }
}

.menu {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 4;
  display: flex;
  visibility: hidden;
  justify-content: flex-end;
  height: 100vh;
  width: 100%;
  transform: translateX(100%);
  transition: transform 0.3s ease, visibility 0.3s ease;
}

.menu__close-field {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 2;
  height: 100%;
  width: 75%;
  opacity: 0;
  background-color: rgba(0, 0, 0, 0.7);
  cursor: pointer;
  transition: opacity 0.3s ease 0.2s;
}

.menu.active-flex {
  display: flex;
}

.menu.active {
  visibility: visible;
  transform: translateX(0%);
}

.menu.active .menu__close-field {
  opacity: 1;
}

.menu__wrapper {
  position: relative;
  z-index: 3;
  overflow-y: auto;
  width: 25%;
  height: 100%;
  padding-top: 67px;
  padding-left: 72px;
  background-color: #fff;
  transition: padding 0.2s ease;
}

.menu__wrapper.menu__wrapper--onscroll {
  padding-top: 20px;
}

.menu__lang-btns {
  margin-bottom: 53px;
  font-size: 24px;
}

.menu__lang-btn {
  border-bottom: 1px solid transparent;
  transition: border-bottom 0.2s ease-out;
}

@media (min-width: 767.98px) {
  .menu__lang-btn:hover {
    border-bottom: 1px solid #000000;
  }
}

.menu__list-category {
  margin-bottom: 40px;
}

.menu__list-category li:not(:last-child) {
  margin-bottom: 15px;
}

.menu__list {
  margin-bottom: 40px;
}

.menu__list li:not(:last-child) {
  margin-bottom: 15px;
}

.menu__link {
  color: black;
  border-bottom: 1px solid transparent;
  font-size: 18px;
  transition: border-bottom 0.2s ease-out;
}

@media (min-width: 767.98px) {
  .menu__link:hover {
    border-bottom: 1px solid #000000;
  }
}

.menu__link--bold {
  display: inline-block;
  color: black;
  font-weight: 700;
  font-size: 24px;
  text-transform: uppercase;
  transition: all 0.2s ease;
}

.menu__list-social {
  display: flex;
}

.menu__list-social li:not(:last-child) {
  margin-right: 15px;
}

.menu__link--inst, .menu__link--fb {
  display: inline-block;
}

.menu__link--inst svg, .menu__link--fb svg {
  width: 22px;
  height: 23px;
}

@media (max-width: 4400px) and (min-width: 2000px) {
  .menu__wrapper {
    width: 45%;
  }
}

@media (max-width: 3400px) {
  .menu__wrapper {
    width: 40%;
  }
}

@media (max-width: 2000px) {
  .menu__wrapper {
    width: 35%;
  }
}

@media (max-width: 991.98px) {
  .menu__wrapper {
    width: 100%;
  }
}

@media (max-width: 767.98px) {
  .menu__wrapper {
    padding-top: 12px;
  }
  .menu__wrapper.menu__wrapper--onscroll {
    padding-top: 12px;
  }
}

@media (max-width: 479.98px) {
  .menu__wrapper {
    padding-left: 30px;
  }
}

.burger {
  position: relative;
  z-index: 5;
  display: block;
  width: 26px;
  height: 20px;
  outline: none;
  background: transparent;
  transition: transform 0.3s ease 0s;
}

.burger__upper-line, .burger__outer-line {
  content: "";
  position: absolute;
  left: 0;
  width: 100%;
  height: 2px;
  background-color: #fff;
  transition: all 0.3s ease 0s;
}

.burger__upper-line {
  top: 0;
}

.burger__middle-line {
  position: absolute;
  left: 0;
  top: 9px;
  width: 100%;
  height: 2px;
  background-color: #fff;
  transition: all 0.3s ease 0s;
}

.burger__outer-line {
  bottom: 0;
}

.burger.active .burger__middle-line {
  background-color: #000;
  transform: scale(0);
}

.burger.active .burger__upper-line {
  top: 9px;
  background-color: #000;
  transform: rotate(45deg);
}

.burger.active .burger__outer-line {
  bottom: 9px;
  background-color: #000;
  transform: rotate(-45deg);
}

@media (min-width: 767.98px) {
  .burger:hover {
    transform: scale(1.1);
  }
  .burger:focus {
    transform: scale(1.3);
  }
}

.promo {
  height: 100%;
  background: url(../img/main-page-bg-promo.jpg) center/cover no-repeat;
  min-height: 100vh;
  display: flex;
  align-items: flex-end;
  justify-content: flex-start;
}

.promo__container {
  padding-top: 150px;
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  padding-bottom: 230px;
}

.promo__title {
  margin-bottom: 20px;
  color: #fff;
}

.promo__title--small {
  display: block;
  font-size: 24px;
  font-weight: 400;
  text-transform: uppercase;
}

.promo__btn {
  align-self: flex-start;
  padding: 6px 26px;
  text-transform: uppercase;
}

@media (max-width: 991.98px) {
  .promo__container {
    padding-bottom: 150px;
