/* Снэк-бар - расположите ее внизу и в середине экрана */
#snackbar-cart {
  visibility: hidden;
  /* Скрыто по умолчанию. Видимый по щелчку мыши */
  min-width: 250px;
  /* Установите минимальную ширину по умолчанию */
  width: 90%;
  margin-left: -125px;
  /* Разделите значение min-width на 2 */
  background-color: #333;
  /* Черный цвет фона */
  color: #fff;
  /* Белый цвет текста */
  text-align: center;
  /* Центрированный текст */
  border-radius: 2px;
  /* Округленные границы */
  padding: 16px;
  /* Отступ */
  position: fixed;
  /* Сядьте на верхнюю часть экрана */
  z-index: 1;
  /* Добавить z-индекс при необходимости */
  right: 5%;
  /* Центр, снэк-бар */
  bottom: 30px;
  /* 30 пикселей снизу */
}

/* Показывать снэк-бар при нажатии на кнопку (Класс добавлен с помощью JavaScript) */
#snackbar-cart.show {
  visibility: visible;
  /* Покажите снэк-бар */
  /* Добавить анимацию: Возьмите 0,5 секунды, чтобы исчезать и выходить из закусочной.
	Однако задержите процесс затухания на 2,5 секунды */
  -webkit-animation: fadein 0.5s, fadeout 0.5s 2.8s;
  animation: fadein 0.5s, fadeout 0.5s 2.8s;
}

#snackbar-rating {
  visibility: hidden;
  /* Скрыто по умолчанию. Видимый по щелчку мыши */
  min-width: 250px;
  /* Установите минимальную ширину по умолчанию */
  width: 90%;
  margin-left: -125px;
  /* Разделите значение min-width на 2 */
  background-color: #333;
  /* Черный цвет фона */
  color: #fff;
  /* Белый цвет текста */
  text-align: center;
  /* Центрированный текст */
  border-radius: 2px;
  /* Округленные границы */
  padding: 16px;
  /* Отступ */
  position: fixed;
  /* Сядьте на верхнюю часть экрана */
  z-index: 1;
  /* Добавить z-индекс при необходимости */
  right: 5%;
  /* Центр, снэк-бар */
  bottom: 30px;
  /* 30 пикселей снизу */
}

/* Показывать снэк-бар при нажатии на кнопку (Класс добавлен с помощью JavaScript) */
#snackbar-rating.show {
  visibility: visible;
  /* Покажите снэк-бар */
  /* Добавить анимацию: Возьмите 0,5 секунды, чтобы исчезать и выходить из закусочной.
	Однако задержите процесс затухания на 2,5 секунды */
  -webkit-animation: fadein 0.5s, fadeout 0.5s 2.8s;
  animation: fadein 0.5s, fadeout 0.5s 2.8s;
}

/* Анимация, чтобы исчезать снэк-бар в и из */
@-webkit-keyframes fadein {
  from {
    bottom: 0;
    opacity: 0;
  }
  to {
    bottom: 30px;
    opacity: 1;
  }
}

@keyframes fadein {
  from {
    bottom: 0;
    opacity: 0;
  }
  to {
    bottom: 30px;
    opacity: 1;
  }
}

@-webkit-keyframes fadeout {
  from {
    bottom: 30px;
    opacity: 1;
  }
  to {
    bottom: 0;
    opacity: 0;
  }
}

@keyframes fadeout {
  from {
    bottom: 30px;
    opacity: 1;
  }
  to {
    bottom: 0;
    opacity: 0;
  }
}
