.header {
	position: relative;
	width: 100%;
}

.nav {
	&::before {
		content: "";
		position: absolute;
		top: 0;
		left: 0;
		z-index: 2;

		width: 100%;
		height: 100%;

		background-color: transparent;

		transition: all 0.2s ease;
	}
	position: fixed;
	top: 0;
	left: 0;
	z-index: 50;

	width: 100%;
	padding-top: 48px;

	transition: all 0.2s ease;

	&.nav--active {
		padding-top: 0px;
		&::before {
			background: #151515;
		}
	}

	&__body {
		position: relative;

		display: flex;
		justify-content: space-between;
		align-items: center;
		height: 100%;

		transition: all 0.2s ease;
	}
	&__logo {
		position: relative;
		z-index: 2;
	}
	&__form-wrapper {
		position: relative;
	}
	&__form {
		position: relative;
		z-index: 4;

		padding-right: 75px;
	}

	&__submit {
		position: absolute;
		top: 50%;
		left: 17px;

		width: 14px;
		height: 14px;

		transform: translateY(-50%);
		svg {
			width: 14px;
			height: 14px;

			transition: transform 0.3s ease;

			@media (min-width: $md3+px) {
				&:hover {
					transform: scale(1.2);
				}
			}
		}
	}
	&__submit:hover .nav__search {
		border: 1px solid $main-color;
	}
	&__search {
		width: 400px;
		height: 30px;
		padding-left: 42px;
		padding-right: 10px;

		outline: none;
		background: #ffffff;
		border-radius: 15.5px;
		border: 1px solid transparent;

		font-size: 18px;
		font-weight: 500;

		transition: border 0.2s ease;
		@media (min-width: $md3+px) {
			&:hover {
				border: 1px solid #000;
			}
			&:focus {
				border: 1px solid #000;
			}
		}
		&.active {
			border-bottom-left-radius: 0;
			border-bottom-right-radius: 0;
		}
	}

	&__icon-btns {
		display: flex;
		align-items: center;
	}
	&__cart-btn {
		position: relative;
		z-index: 7;

		display: flex;
		align-self: center;
		margin-right: 35px;

		outline: none;
		background: transparent;

		transition: all 0.3s ease;
		@media (min-width: $md3+px) {
			&:focus svg {
				transform: scale(1.3);
			}
		}

		svg {
			width: 25px;
			height: 25px;

			fill: white;

			transition: all 0.3s ease;

			@media (min-width: $md3+px) {
				&:hover {
					transform: scale(1.1);
				}
			}
		}
		&.active {
			svg {
				fill: #000;
			}
			background-color: #fff;

			border-radius: 100%;
		}
	}

	@media (max-width: $md2+px) {
		&__form {
			padding-right: 0;
		}
	}
	@media (max-width: $md3+px) {
		padding-top: 5px;
		&.nav--active {
			padding-top: 5px;
		}
		&__form-wrapper {
			width: 100%;
		}
		&__body {
			flex-wrap: wrap;
			padding-bottom: 5px;
		}
		&__icon-btns {
			flex: 0 0 50%;
			display: flex;
			justify-content: flex-end;
		}
		&__logo {
			flex: 0 0 50%;
			img {
				height: 45px;
				width: 120px;
			}
		}
		&__form {
			width: 100%;
		}
		&__search {
			width: 100%;
		}
	}
}
// Cart
.cart {
	position: absolute;
	top: 0;
	left: 0;
	z-index: 6;

	display: flex;
	flex-direction: column;
	align-items: flex-end;
	visibility: hidden;

	height: 100vh;
	width: 100%;

	transform: translateX(100%);
	transition: transform 0.3s ease, visibility 0.3s ease;
	&.active-flex {
		display: flex;
	}
	&.active {
		visibility: visible;
		transform: translateX(0px);
	}
	&.active .cart__close-field {
		opacity: 1;
	}
	&__close-field {
		position: absolute;
		top: 0;
		left: 0;
		z-index: 2;

		height: 100%;
		width: 100%;

		opacity: 0;
		background-color: rgba($color: #000000, $alpha: 0.7);

		cursor: pointer;
		transition: opacity 0.3s ease 0.2s;
	}

	&__wrapper {
		position: relative;
		z-index: 6;
		overflow-y: auto;
		width: 25%;
		height: 100%;
		padding-top: 70px;

		background-color: #fff;

		transition: padding 0.2s ease;
		&.cart__wrapper--onscroll {
			padding-top: 20px;
		}
	}

	&__quantity {
		padding: 0 30px;
		padding-bottom: 5px;

		font-weight: 700;
		font-size: 18px;
	}

	&__list {
	}

	&__item {
		padding: 10px 0;
		padding-right: 24px;
		padding-left: 32px;
		&:not(:last-child) {
			border-bottom: 1px solid black;
		}
	}

	&__sum {
		padding: 0 27px;
		padding-top: 20px;
		padding-bottom: 17px;
		font-weight: 700;
		font-size: 18px;
		text-align: right;
	}

	&__btn-order {
		width: 100%;
		padding-top: 17px;
		padding-bottom: 16px;
	}
	@media (max-width: 4400px) and (min-width: 2000px) {
		&__btn-order {
			padding-left: 32px;
			text-align: left;
		}
		&__wrapper {
			width: 45%;
		}
		&__sum {
			text-align: left;
		}
	}
	@media (max-width: 3400px) and (min-width: 2000px) {
		&__btn-order {
			padding-left: 32px;
			text-align: left;
		}
		&__wrapper {
			width: 40%;
		}
		&__sum {
			text-align: left;
		}
	}
	@media (max-width: 2000px) {
		&__wrapper {
			width: 35%;
		}
	}

	@media (max-width: $md2+px) {
		&__wrapper {
			width: 100%;
		}
	}
	@media (max-width: $md3+px) {
		&__wrapper {
			padding-top: 20px;
			&.cart__wrapper--onscroll {
				padding-top: 20px;
			}
		}
	}
	@media (max-width: $md4+px) {
		&__quantity {
			padding: 0 10px;
		}
		&__item {
			padding-right: 10px;
			padding-left: 10px;
		}
	}
	@media (max-height: 479.98px) and (orientation: landscape) {
		&__quantity {
			padding: 0 50px;
		}
		&__item {
			padding-right: 50px;
			padding-left: 50px;
		}
	}
}

.sum {
	font-weight: 400;
	font-size: 18px;
}

.c-item {
	position: relative;

	display: flex;
	&__image {
		margin-right: 27px;
		width: 63px;
		height: 46px;
		object-fit: contain;
	}

	&__link {
		color: inherit;
	}
	&__title {
		padding: 5px 0;

		font-weight: 500;
		font-size: 18px;
	}

	&__price {
		display: block;
		font-size: 18px;
	}

	&__delete {
		position: absolute;
		top: 0;
		left: 0;
		z-index: 1;
		display: flex;
		justify-content: center;
		align-items: center;
		width: 20px;
		height: 20px;

		background: rgb(236, 142, 142);
		border-radius: 100%;
		font-size: 26px;
		color: #fff;
	}
}

// Search Field
.search-field {
	display: none;
	position: absolute;
	top: 15px;
	left: 0;
	z-index: 3;

	overflow: hidden;
	min-height: 130px;
	width: 400px;
	padding-top: 15px;

	border-bottom-right-radius: 15.5px;
	border-bottom-left-radius: 15.5px;
	background-color: $main-color;
	&__list {
	}

	&__item {
		padding-right: 17px;
		padding-left: 17px;
		padding-top: 8px;
		padding-bottom: 7px;

		&:not(:last-child) {
			border-bottom: 1px solid #000;
		}
	}
	@media (max-width: $md3+px) {
		width: 100%;
	}
}
.sf-item {
	display: flex;
	align-items: center;

	width: 100%;

	transition: background-color 0.3s ease;
	@media (min-width: $md3+px) {
		&:hover {
			background-color: #d5d5d5;
		}
		&:focus {
		}
	}
	&__image {
		margin-right: 10px;
	}

	&__img {
	}

	&__info {
	}
	&__link {
		display: inline-block;
		color: #000;
	}
	&__name {
		display: block;
		max-width: 260px;

		white-space: nowrap; /* Запрещаем перенос строк */
		overflow: hidden; /* Обрезаем все, что не помещается в область */
		text-overflow: ellipsis;

		font-weight: 500;
		font-size: 18px;
		text-transform: uppercase;
	}

	&__subinfo {
		display: block;
		max-width: 260px;

		white-space: nowrap; /* Запрещаем перенос строк */
		overflow: hidden; /* Обрезаем все, что не помещается в область */
		text-overflow: ellipsis;

		font-size: 18px;
	}

	@media (max-width: $md4+px) {
		&__name {
			max-width: 180px;
		}
		&__subinfo {
			max-width: 180px;
		}
	}
}

// Menu
.menu {
	position: absolute;
	top: 0;
	left: 0;
	z-index: 4;

	display: flex;
	visibility: hidden;
	justify-content: flex-end;

	height: 100vh;
	width: 100%;

	transform: translateX(100%);
	transition: transform 0.3s ease, visibility 0.3s ease;

	&__close-field {
		position: absolute;
		top: 0;
		left: 0;
		z-index: 2;

		height: 100%;
		width: 75%;

		opacity: 0;
		background-color: rgba($color: #000000, $alpha: 0.7);

		cursor: pointer;
		transition: opacity 0.3s ease 0.2s;
	}
	&.active-flex {
		display: flex;
	}
	&.active {
		visibility: visible;
		transform: translateX(0%);
	}
	&.active .menu__close-field {
		opacity: 1;
	}
	&__wrapper {
		@extend %scroll-del;

		position: relative;
		z-index: 3;

		overflow-y: auto;
		width: 25%;
		height: 100%;
		padding-top: 67px;
		padding-left: 72px;

		background-color: #fff;

		transition: padding 0.2s ease;
		&.menu__wrapper--onscroll {
			padding-top: 20px;
		}
	}

	&__lang-btns {
		margin-bottom: 53px;

		font-size: 24px;
	}

	&__lang-btn {
		border-bottom: 1px solid transparent;
		transition: border-bottom 0.2s ease-out;
		@media (min-width: $md3+px) {
			&:hover {
				border-bottom: 1px solid #000000;
			}
		}
	}

	&__list-category {
		margin-bottom: 40px;
		li {
			&:not(:last-child) {
				margin-bottom: 15px;
			}
		}
	}

	&__list {
		margin-bottom: 40px;
		li {
			&:not(:last-child) {
				margin-bottom: 15px;
			}
		}
	}

	&__link {
		color: black;
		border-bottom: 1px solid transparent;

		font-size: 18px;

		transition: border-bottom 0.2s ease-out;
		@media (min-width: $md3+px) {
			&:hover {
				border-bottom: 1px solid #000000;
			}
		}
	}
	&__link--bold {
		display: inline-block;
		color: black;

		font-weight: 700;
		font-size: 24px;
		text-transform: uppercase;

		transition: all 0.2s ease;
	}

	&__list-social {
		display: flex;
		li:not(:last-child) {
			margin-right: 15px;
		}
	}
	&__link--inst,
	&__link--fb {
		display: inline-block;
		svg {
			width: 22px;
			height: 23px;
		}
	}
	@media (max-width: 4400px) and (min-width: 2000px) {
		&__wrapper {
			width: 45%;
		}
	}
	@media (max-width: 3400px) {
		&__wrapper {
			width: 40%;
		}
	}
	@media (max-width: 2000px) {
		&__wrapper {
			width: 35%;
		}
	}

	@media (max-width: $md2+px) {
		&__wrapper {
			width: 100%;
		}
	}
	@media (max-width: $md3+px) {
		&__wrapper {
			padding-top: 12px;
			&.menu__wrapper--onscroll {
				padding-top: 12px;
			}
		}
	}
	@media (max-width: $md4+px) {
		&__wrapper {
			padding-left: 30px;
		}
	}
}

// Burger
.burger {
	position: relative;
	z-index: 5;

	display: block;
	width: 26px;
	height: 20px;

	outline: none;
	background: transparent;

	transition: transform 0.3s ease 0s;

	&__upper-line,
	&__outer-line {
		content: "";
		position: absolute;
		left: 0;

		width: 100%;
		height: 2px;

		background-color: #fff;

		transition: all 0.3s ease 0s;
	}

	&__upper-line {
		top: 0;
	}
	&__middle-line {
		position: absolute;
		left: 0;
		top: 9px;

		width: 100%;
		height: 2px;

		background-color: #fff;

		transition: all 0.3s ease 0s;
	}

	&__outer-line {
		bottom: 0;
	}

	//Burger Active
	&.active .burger__middle-line {
		background-color: #000;
		transform: scale(0);
	}
	&.active .burger__upper-line {
		top: 9px;
		background-color: #000;
		transform: rotate(45deg);
	}
	&.active .burger__outer-line {
		bottom: 9px;
		background-color: #000;
		transform: rotate(-45deg);
	}
	@media (min-width: $md3+px) {
		&:hover {
			transform: scale(1.1);
		}
		&:focus {
			transform: scale(1.3);
		}
	}
}
