.second-wrapper {
	padding-top: 90px;
	background: url(../img/sec-bg.svg) 0 0 / contain no-repeat;
}
@media (max-width: 1439px) {
	.second-wrapper {
		background: url(../img/sec-bg.svg) 0 0 / cover no-repeat;
	}
}
@media (max-width: $md2+px) {
	.second-wrapper {
		padding-top: 60px;
	}
}
.pluses {
	padding-bottom: 167px;
	&__container {
		max-width: 1188px;
	}

	&__title {
		text-align: center;
		margin-bottom: 160px;
	}

	&__row {
		display: grid;
		grid-template-columns: repeat(3, 1fr);
		grid-gap: 80px;
	}

	@media (max-width: $md2+px) {
		padding-bottom: 100px;
		&__title {
			margin-bottom: 100px;
		}
		&__row {
			grid-gap: 30px;
		}
	}
	@media (max-width: $md3+px) {
		padding-bottom: 60px;
		&__row {
			grid-template-columns: 1fr;
			grid-gap: 100px;
		}
	}
}

.p-block {
	&__wrapper {
		position: relative;

		width: 100%;
		height: 100%;

		padding: 0 18px;
		padding-top: 57px;
		padding-bottom: 22px;
		background-color: #f2f2f2;

		text-align: center;
	}
	&__image {
		position: absolute;
		top: -93px;
		left: 50%;

		width: 133px;
		height: 133px;
		object-fit: contain;

		transform: translateX(-50%);
	}
	&__title {
		margin-bottom: 13px;

		font-size: 24px;
		font-weight: 700;
	}
}

// News
.news {
	margin-bottom: 90px;
	&__container {
		max-width: 1300px;
	}

	&__title {
		margin-bottom: 60px;

		text-align: center;
	}
	@media (max-width: $md2+px) {
		&__title {
			margin-bottom: 40px;
		}
	}
	@media (max-width: $md4+px) {
		margin-bottom: 20px;
	}
}

// Swiper buttnos
.swiper-button-next {
	background: transparent;
}
.swiper-button-prev {
	background: transparent;
}
.news-swiper-btn-next {
	right: 0;
}
.news-swiper-btn-next::after {
	color: black;
}
.news-swiper-btn-prev {
	left: 0;
}
.news-swiper-btn-prev::after {
	color: black;
}

// Swiper Slide
.n-slide {
	display: flex;
	flex-direction: column;
	height: 100%;
	margin: 0 30px;

	background-color: #f2f2f2;

	&__image {
		max-width: 100%;
		max-height: 288px;
		object-fit: cover;
	}

	&__info {
		display: flex;
		flex-direction: column;
		height: 100%;

		text-align: center;
	}

	&__title {
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
		flex: 1 0 auto;

		padding: 0 5px;
		padding-top: 25px;
		margin-bottom: 21px;

		font-weight: 700;
		font-size: 24px;
		line-height: 31px;
	}

	&__text {
		flex: 0 0 auto;

		padding: 0 5px;
		margin-bottom: 46px;
	}

	&__link {
		flex: 0 0 auto;
	}
	@media (max-width: 1300px) {
		&__title {
			padding-top: 10px;
			margin-bottom: 10px;
		}

		&__text {
			margin-bottom: 10px;
		}
	}
}

// Mail Form
.mail-form {
	background: url(../img/mail-bg.png) 40px 0 / contain no-repeat;
	padding-top: 14%;
	padding-bottom: 15.7%;
	&__container {
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
		text-align: center;
	}

	&__title {
		margin-bottom: 25px;
	}

	&__form {
		width: 415px;
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
	}

	&__input {
		width: 100%;
		margin-bottom: 30px;
	}

	&__submit {
		padding: 8px 56px;
	}

	@media (max-width: $md3+px) {
		&__form {
			width: 100%;
		}
	}
}
