// Base
html {
	min-height: 100%;
	height: 100%;
}
body {
	position: relative;
	height: 100%;
	min-height: 100%;

	font-family: $ff, sans-serif;
	font-weight: 400;
}
.wrapper {
	min-height: 100vh;
	display: flex;
	flex-direction: column;
}

.lock {
	overflow: hidden;
}

.container {
	max-width: $maxWidthContainer + 30px;
	padding-left: 15px;
	padding-right: 15px;
	margin: 0 auto;
}

@media (max-width: $md2+px) and (orientation: portrait) {
	.container {
		padding-left: 10px;
		padding-right: 10px;
	}
}

@media (max-width: $md3+px) and (orientation: portrait) {
	.container {
		padding-left: 10px;
		padding-right: 10px;
	}
}

@media (max-height: $md4+px) and (orientation: landscape) {
	.container {
		padding-left: 50px;
		padding-right: 50px;
	}
}
// -- \\

// Common
.visually-hidden {
	position: absolute !important;
	clip: rect(1px 1px 1px 1px); /* IE6, IE7 */
	clip: rect(1px, 1px, 1px, 1px);
	padding: 0 !important;
	border: 0 !important;
	height: 1px !important;
	width: 1px !important;
	overflow: hidden;
}

.logo {
	color: #fff;

	font-weight: 700;
	transition: transform 0.3s ease;
	img {
		height: 65px;
		width: 160px;
	}
	@media (min-width: $md3+px) {
		&:hover {
			transform: scale(1.05);
		}
		&:focus {
			transform: scale(1.05);
		}
	}
}
.main-title {
	font-family: $ff, sans-serif;
	font-weight: 700;
	font-size: $fz-heading;
	color: $col-black;
	@media (max-width: $md1+px) {
		font-size: 50px;
	}
	@media (max-width: $md2+px) {
		font-size: 36px;
	}
	@media (max-width: $md4+px) {
		font-size: 26px;
	}
}
.sub-title {
	color: $col-black;

	font-family: $ff, sans-serif;
	font-weight: 700;
	font-size: 36px;
	@media (max-width: $md2+px) {
		font-size: 26px;
	}
	@media (max-width: $md3+px) {
		font-size: 26px;
		font-size: 20px;
	}
}
.text {
	font-family: $ff, sans-serif;
	font-size: $fz-text;
	line-height: 23px;
}
.btn-reset {
	border: none;
	margin: 0;
	padding: 0;
	width: auto;
	overflow: visible;
	background: transparent;
	color: inherit;
	font: inherit;
	line-height: normal;
	-webkit-font-smoothing: inherit;
	-moz-osx-font-smoothing: inherit;
	-webkit-appearance: none;
	&::-moz-focus-inner {
		border: 0;
		padding: 0;
	}
}

.btn-link-black {
	display: inline-block;
	padding-top: 11px;
	padding-bottom: 11px;

	color: #fff;
	background-color: #000;
	border: 3px solid #000000;

	text-align: center;
	text-transform: uppercase;
	font-weight: 700;
	font-size: 18px;
	line-height: 1;

	transition: all 0.3s ease;
	&:hover {
		color: #000;
		background-color: rgb(255, 255, 255);
	}
	&:focus {
		color: #000;
		background-color: rgb(255, 255, 255);
	}
	&:disabled {
		color: #fff;
		background-color: #000;
		border: 3px solid #000000;
		opacity: 0.5;
		cursor: default;
	}
}

.btn-link-transparent {
	display: inline-block;
	width: 100%;
	padding-top: 11px;
	padding-bottom: 11px;

	color: #000;
	background-color: transparent;
	border: 3px solid #000000;

	text-align: center;
	text-transform: uppercase;
	font-weight: 700;
	font-size: 18px;
	line-height: 1;

	transition: all 0.3s ease;
	&:hover {
		color: #fff;
		background-color: #000000;
	}
	&:focus {
		color: #fff;
		background-color: #000000;
	}
	&:disabled {
		color: #fff;
		background-color: #000;
		border: 3px solid #000000;
		opacity: 0.5;
		cursor: default;
	}
}

.input {
	display: inline-block;
	height: 40px;
	padding-left: 20px;

	color: #000;
	background-color: transparent;
	border: 3px solid #000000;
	outline: none;

	font-size: 18px;
	font-weight: 400;
	text-transform: none;

	transition: all 0.3s ease;
	&::placeholder {
		font-family: $ff, sans-serif;
	}
	&:hover {
		color: #fff;
		background-color: #000000;
	}
	&:focus {
		color: #fff;
		background-color: #000000;
	}
}

.copy {
	color: #484848;

	font-weight: 500;
	font-size: 18px;
	text-align: center;
}
