.first-wrapper {
	padding-top: 118px;
	background: url(../img/first-bg.svg) 0 0 / cover no-repeat;
}
@media (max-width: $md2+px) {
	.first-wrapper {
		padding-top: 60px;
	}
}
// Category
.category {
	margin-bottom: 168px;
	&__container {
	}

	&__title {
		margin-bottom: 29px;
	}
	@media (max-width: $md2+px) {
		margin-bottom: 60px;
	}
}
// Swiper buttons
.c-swiper-button-next {
	right: 6.5%;
	color: #fff;
	background: transparent;
}

.c-swiper-button-prev {
	left: 6.5%;
	color: #fff;
	background: transparent;
}

.c-swiper-button-prev::after,
.c-swiper-button-next::after {
	font-size: 28px;
}

// Swiper content
.c-slide {
	&__wrapper {
		position: relative;
		z-index: 1;
		overflow: hidden;

		width: 100%;
		height: 100%;

		transform: skew(-25deg);
		&::after {
			content: "";
			position: absolute;
			top: 0;
			left: -50%;

			width: 200%;
			height: 100%;

			transform: skew(25deg);
			filter: brightness(40%);
		}
	}
	&__wrapper--road {
		&::after {
			background: url(../img/category/road-bg.jpg) center/cover no-repeat;
		}
	}
	&__wrapper--street {
		&::after {
			background: url(../img/category/street-bg.jpg) center/cover no-repeat;
		}
	}
	&__wrapper--offroad {
		&::after {
			background: url(../img/category/offroad-bg.jpg) center/cover no-repeat;
		}
	}
	&__link {
		position: relative;
		z-index: 3;

		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;

		padding-top: 45px;
		padding-bottom: 25px;

		transform: skew(25deg);

		transition: transform 0.3s ease;
		@media (min-width: $md3+px) {
			&:hover {
				transform: skew(25deg) scale(1.1);
			}
			&:focus {
				transform: skew(25deg) scale(1.1);
			}
		}
	}

	&__image {
		width: 360px;
		height: 184px;
		object-fit: contain;
		margin-bottom: 29px;
	}

	&__title {
		color: #fff;

		-webkit-text-stroke: 1px #fff;
		-webkit-text-fill-color: transparent;
		font-weight: bold;
		font-size: 36px;
		text-transform: uppercase;
	}
	@media (max-width: $md3+px) {
		&__wrapper {
			position: relative;
			z-index: 1;
			overflow: hidden;

			width: 100%;
			height: 100%;

			transform: skew(0deg);
			&::after {
				content: "";
				position: absolute;
				top: 0;
				left: -50%;

				width: 200%;
				height: 100%;

				transform: skew(0deg);
				filter: brightness(40%);
			}
		}
		&__image {
			max-width: 100%;
			height: 130px;
			object-fit: contain;
			margin-bottom: 29px;
		}
		&__link {
			transform: skew(0deg);
		}
	}
}
// -- //

// Favorite
.favorite {
	padding-bottom: 157px;
	&__container {
	}

	&__title {
		margin-bottom: 28px;
	}

	&__row {
		display: grid;
		grid-template-columns: repeat(12, 1fr);
		grid-auto-rows: minmax(160px, auto);
		grid-gap: 30px;
	}

	&__item {
		&:nth-child(5n + 1) {
			grid-column: 1 / span 6;
			grid-row: 1 / span 2;
		}
		&:nth-child(5n + 2) {
			grid-column: 7 / span 3;
			grid-row: 1 / span 1;
		}
		&:nth-child(5n + 3) {
			grid-column: 10 / span 3;
			grid-row: 1 / span 1;
		}
		&:nth-child(5n + 4) {
			grid-column: 7 / span 3;
			grid-row: 2 / span 1;
		}
		&:nth-child(5n + 5) {
			grid-column: 10 / span 3;
			grid-row: 2 / span 1;
		}

		background-color: $sub-color;
	}

	@media (max-width: 1100px) {
		&__item {
			&:nth-child(5n + 1) {
				grid-column: 1 / span 12;
				grid-row: 1 / span 2;
			}
			&:nth-child(5n + 2) {
				grid-column: 1 / span 6;
				grid-row: 3 / span 1;
			}
			&:nth-child(5n + 3) {
				grid-column: 7 / span 6;
				grid-row: 3 / span 1;
			}
			&:nth-child(5n + 4) {
				grid-column: 1 / span 6;
				grid-row: 4 / span 1;
			}
			&:nth-child(5n + 5) {
				grid-column: 7 / span 6;
				grid-row: 4 / span 1;
			}
		}
	}
	@media (max-width: $md2+px) {
		padding-bottom: 60px;
	}
	@media (max-width: $md3+px) {
	}
	@media (max-width: $md4+px) {
		&__row {
			display: grid;
			grid-template-columns: repeat(1, 1fr);
			grid-gap: 30px;
		}
		&__item {
			&:nth-child(5n + 1) {
				grid-column: 1 / span 1;
				grid-row: 1 / span 1;
			}
			&:nth-child(5n + 2) {
				grid-column: 1 / span 1;
				grid-row: 2 / span 1;
			}
			&:nth-child(5n + 3) {
				grid-column: 1 / span 1;
				grid-row: 3 / span 1;
			}
			&:nth-child(5n + 4) {
				grid-column: 1 / span 1;
				grid-row: 4 / span 1;
			}
			&:nth-child(5n + 5) {
				grid-column: 1 / span 1;
				grid-row: 5 / span 1;
			}
		}
	}
}
.f-b-item {
	&__link {
		position: relative;

		display: flex;
		flex-direction: column;
		align-items: flex-end;

		padding: 63px 0;
		padding-right: 73px;
		padding-left: 20px;

		height: 100%;
		width: 100%;

		background: url(../img/fav-back.png) 0 100% / 67% no-repeat;
		color: #fff;

		text-align: right;

		@media (min-width: $md3+px) {
			&:hover .f-b-item__title {
				transform: scale(1.05);
			}
			&:hover .f-b-item__text {
				transform: scale(1.05);
			}
			&:hover .f-b-item__arrow {
				transform: scale(1.05);
			}
			&:focus .f-b-item__title {
				transform: scale(1.05);
			}
			&:focus .f-b-item__text {
				transform: scale(1.05);
			}
			&:focus .f-b-item__arrow {
				transform: scale(1.05);
			}
		}
	}

	&__title {
		padding-bottom: 5px;
		color: #fff;

		font-size: 36px;
		font-weight: 700;
		text-transform: uppercase;
		transition: transform 0.3s ease;
		@media (max-width: $md3+px) {
			font-size: 24px;
		}
	}

	&__text {
		font-size: 18px;
		line-height: 23px;
		transition: transform 0.3s ease;
	}

	&__arrow {
		position: absolute;
		bottom: 53px;
		right: 42px;

		width: 30px;
		height: 30px;

		background: url(../img/arrow.svg) center/cover no-repeat;

		transition: transform 0.3s ease;
		@media (min-width: $md3+px) {
			&:hover {
				transform: scale(1.3);
			}
			&:focus {
				transform: scale(1.3);
			}
		}
		@media (max-width: 1100px) {
			bottom: 20px;
			right: 70px;
		}
		@media (max-width: $md4+px) {
			right: 20px;
		}
	}

	@media (max-width: $md4+px) {
		&__link {
			padding: 63px 20px;
		}

		&__text {
			font-size: 14px;
			line-height: 23px;
		}
	}
}
.f-item {
	&__link {
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: flex-start;

		padding: 0 30px;
		padding-bottom: 32px;
		padding-top: 30px;
		height: 100%;
		width: 100%;

		color: #fff;

		transition: transform 0.3s ease;
		@media (min-width: $md3+px) {
			&:hover {
				transform: scale(1.05);
			}
			&:focus {
				transform: scale(1.1);
			}
		}
	}

	&__image {
		width: 110px;
		height: 100px;
		object-fit: contain;
		margin-bottom: 24px;
	}

	&__title {
		font-size: 24px;
		text-align: center;
	}
}

// Banner
.banner {
	background-color: #f2f2f2;
	&__container {
		padding-top: 50px;
		padding-bottom: 70px;
	}

	&__text {
		&:not(:last-child) {
			margin-bottom: 30px;
		}
		line-height: 31px;
		font-size: 24px;
		text-align: justify;
	}
	@media (max-width: $md3+px) {
		&__text {
			font-size: 16px;
		}
	}
}
